import React, {useContext, useState} from 'react';
import {Button, Container,Form, Row} from "react-bootstrap";
import Card from "react-bootstrap/Card";
import {NavLink, useLocation, useNavigate} from "react-router-dom";
import {HOME_ROUTE, LOGIN_ROUTE, REGISTRATION_ROUTE} from "../../utils/consts";
import {Facebook, Github, Google, Twitter} from 'react-bootstrap-icons';

import 'macro-css';


import s from "../../components/Header/Header.module.scss";
import {login, registration} from "../../http/userAPI";
import {observer} from "mobx-react-lite";
import {Context} from "../../index";

const Auth = observer(() => {
    const {user} = useContext(Context)
    const location = useLocation()
    const navigate = useNavigate()
    const isLogin = location.pathname === LOGIN_ROUTE
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    const click = async () => {
        try{
            let data;
            if(isLogin){
                data = await login(email, password);
            } else {
                data = await registration(email, password);
            }
            user.setUser(user)
            user.setIsAuth(true)
            navigate.push(HOME_ROUTE)
        } catch (e){
            alert(e.response.data.message)
        }
    }

    return (
        <div>
            <Container
                className="d-flex justify-center align-center"
                style={{height: window.innerHeight - 54}}
            >
                <Card style={{width:600}} className="p-5">
                    <h1 className="text-center ">{isLogin ? 'Sign in' : "Registry"}</h1>
                    <Form className="d-flex flex-column">
                        <Form.Control
                            className="mt-10 mb-10 form-outline"
                            type="email"
                            size="lg"
                            value={email}
                            onChange={e => setEmail(e.target.value)}
                            placeholder="Enter your email..."
                        />
                        <Form.Control
                            className="mb-10"
                            type="password"
                            size='lg'
                            value={password}
                            onChange={e => setPassword(e.target.value)}
                            placeholder="Enter your password..."
                        />

                        <Row className="d-flex mt-3 pr-3">

                            {isLogin ?
                                <div className={s.size}>
                                    No account? <NavLink to={REGISTRATION_ROUTE}>Register!</NavLink>
                                </div>
                                :
                                <div className={s.size}>
                                    Have account? <NavLink to={LOGIN_ROUTE}>Enter!</NavLink>
                                </div>
                            }
                            <div className="">
                                <Button
                                    className={s.enter}
                                    variant={"outline-success"}
                                    onClick={click}
                                >
                                    {isLogin ? 'Enter' : "Register"}
                                </Button>
                            </div>

                        </Row>

                        <Row className={s.size}>
                            {isLogin ?
                                <div>
                                    Or sign up with:
                                </div>
                                :
                                <div>
                                    Or registy with:
                                </div>
                            }

                            <Button type="button" className={s.logos}>
                                <Facebook width="50" height="50"/>
                            </Button>

                            <Button type="button" className={s.logos}>
                                <Twitter width="50" height="50"/>
                            </Button>

                            <Button type="button" className={s.logos}>
                                <Google width="50" height="50"/>
                            </Button>

                            <Button type="button" className={s.logos}>
                                <Github width="50" height="50"/>
                            </Button>

                        </Row>

                    </Form>
                </Card>
            </Container>
        </div>
    );
});

export default Auth;